function between(x: number, [min, max]: [number, number]) {
    if (min > max) max = [min, min = max][0];

    return x >= min && x <= max;
}

export default {
    components: { InboxOutlined },
    setup() {
        const store = useStore();

        const state = reactive({
            VAImages: computed(() => {
                return store.state.VAImages as VAImage[];
            }),
            view: {} as ve.Result
        });

        watch(
            () => state.VAImages,
            () => {
                console.log("here");
            }
        );

        const renderVis = async () => {
            yourVlSpec.data.values = state.VAImages as any;
            const view = await vegaEmbed("#vis", JSON.parse(JSON.stringify(yourVlSpec)));

            view.view.addSignalListener("brush_tuple", (signal: any, value) => {
                // v, e example: state.view.view.signal('click_tuple', {"unit":"","fields":[{"field":"weather","channel":"x","type":"E"}],"values":["sun"]}).run();
                console.log(signal, value);

                let data = JSON.parse(JSON.stringify(state.VAImages)) as VAImage[];

                value.fields.forEach((field: any, index: number) => {
                    const f = field.field;
                    const range = value.values[index];

                    data = data.filter((x) => between((x as any)[f], range));
                });

                store.commit('setSelectedImages', data.map( x=>x.id));
                console.log(data.length);
            });
        };

        renderVis();

        return {
            ...toRefs(state),
        };
    },
};
