
import { onMounted, computed, reactive, toRefs, h, watch } from "vue";
import { InboxOutlined } from "@ant-design/icons-vue";
import { message } from "ant-design-vue";
import { defineComponent, ref } from "vue";
import * as _ from "underscore";
import { useStore, VAImage } from "@/store/index";

export default {
    components: { InboxOutlined },
    setup() {
        const store = useStore();

        const state = reactive({
            VAImages: computed(() => {
                return store.state.VAImages as VAImage[];
            }),
            ifImageShow: computed(() => {
                return store.state.ifImageShow;
            }),
        });

        return {
            ...toRefs(state),
        };
    },
};
